.container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    justify-items: center;
    padding:0 150px;
    grid-gap:20px;
  }
  
  @media (max-width: 750px) {
  
    .container {
      grid-template-columns: 1fr;
      padding:0 100px;
    }
  
  }