.projects-section{
  position: relative;
  width:100%;
  height:fit-content;
  padding-top: 50px;
  /* padding-bottom: 20px; */
  text-align: center;
  justify-items: center;
  align-items: center;
  background-color: rgb(242,242,242);
}


.underline-small{
  max-width: 640px;
  margin: 0 auto 6rem auto;
  /* font-style: italic; */
  padding-top: 20px;
  font-weight: 100;
  color:rgb(51,51,51);
  display:inline-block;
  position:relative;
  
}

.underline-small::after{
  content:'';
  height:5px;
  width:40%;
  background:rgb(1,117,227);
  position:absolute;
  left:30%;
  bottom:-10px;
}


.curve{
  position:absolute;
  height:250px;
  width:100%;
  bottom:0;
  transform: scaleX(1.1);
  /* overflow-y: scroll; */
}

.curve::before{
  content: '';
  display:block;
  position: absolute;
  /* background-color: rgb(1,35,73); */
  /* background-color: red; */
  border-radius: 0 0 100% 100%;
  width: 100%;
  height:100%;
  z-index: 2;
  transform: translate(0%, 50%);
  background-color: rgb(242,242,242);
}

.curve::after{
  content: '';
  display:block;
  position: absolute;
  background-color: rgb(1,35,73);
  /* background-color: red; */
  border-radius: 0 0 100% 100%;
  width: 100%;
  height:100%;
  z-index: 1;
  transform: translate(0%, 58%);
  background-color: rgb(0, 111, 217);
}
